import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../../components/subject/SubjectSummary'
import { SubjectContent } from '../../../components/subject/SubjectContent'
import { SubjectTitle } from '../../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../../components/subject/SubjectIdentifier'
import { Subject } from '../../../components/subject/Subject'

export function Madness () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>rules-sanity-madness</SubjectIdentifier>
      <SubjectKeyword>règle</SubjectKeyword>
      <SubjectTitle>Folie</SubjectTitle>
      <SubjectSummary>

      </SubjectSummary>
      <SubjectContent>
        <p>
          Quand le désespoir amène à la folie, la victime doit alors choisir un
          comportement à adopter parmis la liste des folies suivante :
        </p>

        <p>
          <em>Terreur.</em> Parfois l'instinct de survie est bien plus fort que
          la meilleure des amitiés. Quand un personnage sombre dans la peur la
          plus glaçante il double sa vitesse de déplacement mais est obligé de
          fuir la scène de combat à toutes jambes quitte à se jetter dans la
          gueule d'un piège, ou a se retrouver à des kilomètres de ses
          compagnons. Un personnage qui ne peut plus fuir est obligé de se
          recroqueviller en attendant son heure. Vous avez le droit d'hurler et
          de pleurer si cela peut vous y aider.
        </p>

        <p>
          <em>Colère.</em> Décidément, cette réalité n'a jamais été de votre
          côté, et vous la contesterez jusqu'au bout en agitant vos bras et en
          vidant vos poumons si nécéssaire. Un personnage en état de colère
          profonde ne peut plus réagir aux appels de ses alliés, ne fuira pas le
          champ de bataille et ne pourra pas réaliser une action sans frapper ou
          achever un adversaire. Si le personnage doit choisir un adversaire à
          charger, il doit au préalable tenter une résolution par opposition
          entre son score de contrôle et un degré de difficulté de 10 points, en
          cas d'échec, il est obligé de frapper le personnage le plus proche.
          Vos dégâts sont doublés.
        </p>

        <p>
          <em>Orgueil.</em> Le problème, ça a toujours été les autres. Si vous
          êtes à moins de neuf mètres d'un allié que vous pouvez percevoir, vous
          ne pouvez résister à l'envie de l'insulter, celui-ci doit alors
          réussir un test de volonté d'un degré de difficulté de 10 points pour
          ne pas perdre deux points de volonté par round. De plus, vous êtes
          déconcentré par cette présence non-souhaité ce qui entraîne un malus
          de deux points à tout vos jets. Si vous êtes seul dans un rayon de
          neuf mètres, vous gagnez deux points bonus à tout vos jets.
        </p>
      </SubjectContent>
    </Subject>
  )
}
